<template>
  <el-dialog
    v-model="dialogVisible"
    :title="$t('swap.switchNode')"
    width="620px"
    custom-class="rpc"
  >
    <div class="flex-column-start rpc-content">
        <div class="flex-row-start rpc-add" v-if="connectType == 3">
            <input type="text" :placeholder="$t('swap.rpcplaceholder')" class="rpc-add__input flex-1" v-model="rpcTxt" />
            <el-button class="rpc-add__use" :disabled="!rpcTxt" @click="addHandler">{{ t('swap.add') }}</el-button>
        </div>
        <div class="flex-h__1 rpc-list">
            <div 
                class="rpc-list__item pointer flex-row-start align-center pointer"
                :class="{'active': item.rpc == currentRpc && connectType == 3}"
                v-for="(item, index) in rpcList"
                :key="index"
                @click="selectHandler(item)"
            >
                <div class="token-list__item-title flex-1">{{item.rpc}}</div>
                <div class="token-list__item-desc">
                    <template v-if="item.state == 0">
                        Loading...
                    </template>
                    <template v-else-if="item.state == 1">
                        {{ t('swap.networkSpeed') }}：{{item.speed}}ms
                    </template>
                    <span v-if="item.state == -1" class="text-red">
                        Error
                    </span>
                </div>
                <el-icon color="#f4ba0a" size="22" class="ML-10" v-if="item.rpc == currentRpc && connectType == 3"><Check /></el-icon>
                <div class="token-list__item-copy" @click.stop="copyHandler(item.rpc)" v-if="connectType != 3">{{ t('swap.rpcCopy') }}</div>
                <div class="token-list__item-copy" @click="selectHandler(item)" v-else-if="item.state == 1 && item.rpc != currentRpc">{{ t('swap.switch') }}</div>
                <el-icon color="#fff" class="ML-10" v-if="item.isAdd" @click="removeRpc(index)"><Close /></el-icon>
            </div>
        </div>
    </div>
  </el-dialog>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import copy from 'copy-text-to-clipboard'
import { ElNotification } from 'element-plus'
import axios from 'axios'
import i18n from '@/locals/index.js'
const { t } = i18n.global
export default {
    name: 'History',
    data() {
        return {
            dialogVisible: false,
            connectType: '',
            rpcTxt: '',
            rpcList: []
        }
    },
    computed: {
        ...mapGetters(['currentNetwork', 'currentDex', 'currentRpc']),
    },    
    methods: {
        ...mapActions(['setCurrentRpc']),
        getNetwordSpeed(rpc) {
            const date1 = (new Date()).valueOf()
            const index = this.rpcList.findIndex(item => item.rpc === rpc)
            axios.post(rpc, {"jsonrpc":"2.0","method":"web3_clientVersion","params":[],"id":67
            }).then(data => {
                this.rpcList[index].state = 1
            }).catch(err => {
                this.rpcList[index].state = -1
            }).finally(() => {
                const date2 = (new Date()).valueOf()
                this.rpcList[index].speed = date2 - date1
            })
        },
        removeRpc(index) {
            this.rpcList.splice(index, 1)
            localStorage.setItem('rpcList' + this.currentDex.address, JSON.stringify(this.rpcList))
        },
        addHandler() {
            const item = this.rpcList.find(item => item.rpc === this.rpcTxt)
            if (item) {
                ElNotification({
                    type: 'error',
                    title: 'Error',
                    message: t('swap.exists')
                })
                return
            }

            this.rpcList.unshift({
                rpc: this.rpcTxt,
                speed: 0,
                state: 0,
                isAdd: true,
            })
            this.getNetwordSpeed(this.rpcTxt)
            ElNotification({
                type: 'success',
                title: 'Success',
                message: t('swap.rpcAddSuccess')
            })
            this.rpcTxt = ''
            localStorage.setItem('rpcList' + this.currentDex.address, JSON.stringify(this.rpcList))
        },
        open(type) {
            this.dialogVisible = true
            this.connectType = type
            const rpcs = localStorage.getItem('rpcList' + this.currentDex.address) ? JSON.parse(localStorage.getItem('rpcList' + this.currentDex.address)) : this.currentNetwork.Rpcs
            this.rpcList = rpcs.map(item => {
                return {
                    rpc: item.rpc || item,
                    speed: 0,
                    state: 0
                }
            })
            this.rpcList.forEach(item => {
                this.getNetwordSpeed(item.rpc)
            })
        },
        copyHandler(text){
            copy(text)
            ElNotification({
                type: 'success',
                title: '复制成功',
                message: t('swap.rpcCopySuccess')
            })
        },
        // 切换到该rpc
        selectHandler(item) {
            if (this.connectType == 3 && item.state == 1) {
                this.setCurrentRpc(item.rpc)
                window.wallet.switchRpc(item.rpc)
                ElNotification({
                    type: 'success',
                    title: 'Success',
                    message: t('swap.switchSuccess')
                })
            }
        }
    }
}
</script>

<style lang="scss">
.rpc-add{
    padding: 20px 20px 10px;
    .rpc-add__input{
        background: none;
        border: 1px solid #464646;
        border-radius: 5px;
        padding: 0 10px;
        color: #fff;
        font-size: 16px;
    }
    .rpc-add__use{
        background: #F4BA0A;
        border-color: #F4BA0A;
        color: #464646;
        margin-left: 15px;
        &.is-disabled{
            color: #c0c4cc;
            cursor: not-allowed;
            background-image: none;
            background-color: #ffffff;
            border-color: #e4e7ed;
        }
    }
}
.rpc-list__import-name{
    font-size: 16px;
    font-weight: bold;
    color: #fff;
}
.rpc-list__import-desc{
    font-size: 14px;
    color: rgb(165, 165, 165);
}
.rpc-list__import-btn,.rpc-list__import-btn:hover{
    background: #FCBE2F;
    border: 1px solid #2A2A2A;
    color: #fff;
}
    .rpc{
        background: rgb(39, 38, 44);
        border-radius: 32px;
        max-height: 100vh;
        max-width: 100%;
        display: flex;
        flex-direction: column;

        .el-dialog__close{
            font-size: 20px;
            font-weight: bold;
        }
        .el-dialog__title{
            color: #FFF;
        }
        .el-dialog__header{
            border-bottom: 1px solid #5d5d5d;
        }
        .el-dialog__body{
            flex: 1;
            height: 100%;
            overflow: hidden;
            padding: 0;
        }
        .rpc-content{
            height: 100%;
        }
        .rpc-input{
            background-color: rgb(55, 47, 71);
            border-radius: 16px;
            color: rgb(244, 238, 255);
            display: block;
            font-size: 16px;
            height: 48px;
            outline: 0px;
            padding: 0px 16px;
            width: calc(100% - 60px);
            border: 1px solid rgb(38, 33, 48);
            margin: 20px;
        }
        .rpc-list{
            overflow: auto;
            max-height: 400px;
            padding: 0 20px 20px;
            &::-webkit-scrollbar {
                width: 8px;
            }
            &::-webkit-scrollbar-thumb {
                background: rgb(184, 173, 210);
                border-radius: 8px;
            }
            &::-webkit-scrollbar-track {
                border-radius: 10px;
            }
            .rpc-list__item{
                padding: 10px;
                transition: all .3s;
                background: rgb(0, 0, 0);
                border: 1px solid #000;
                border-radius: 8px;
                margin-top: 10px;
                &.active{
                    border: 1px solid rgba(244, 186, 10, 1);
                }
            }
            .rpc-list__item-icon{
                width: 24px;
                height: 24px;
            }
            .token-list__item-title{
                color: #fff;
                font-size: 16px;
            }
            .token-list__item-desc{
                color: #F4BA0A;
                margin-left: 10px;
            }
            .token-list__item-copy{
                width: 50px;
                height: 26px;
                line-height: 23px;
                border-radius: 15px;
                border: 1px solid #F4BA0A;
                color: #F4BA0A;
                text-align: center;
                margin-left: 10px;
            }
            .token-list__item-price{
                font-size: 14px;
                color: #fff;
            }
        }
    }
</style>